import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Sodoku from '../components/Sodoku.vue'
import Operation from '../components/Operation.vue'
import Typing from '../components/Typing.vue'
import Greeting from '../components/Greeting.vue'
import Input from '../components/typing/Input.vue'
import Result from '../components/typing/Result.vue'
import Settings from '../components/typing/Settings.vue'
import Simplex from '../components/operation/Simplex.vue'


export default new VueRouter({
    routes: [
        {
            path: '/',
            redirect: '/greeting'
        },
        {
            path: '/greeting',
            component: Greeting,
        },
        {
            path: '/sodoku',
            component: Sodoku
        },
        {
            path: '/operation',
            component: Operation,
            children: [
                {
                    path: '',
                    redirect: 'simplex'
                },
                {
                    path: 'simplex',
                    name: 'simplex',
                    component: Simplex
                }
            ]
        },
        {
            path: '/typing',
            component: Typing,
            children: [
                {
                    path: '',
                    redirect: 'input'
                },
                {
                    path: 'input',
                    name: 'input',
                    component: Input
                },
                {
                    path: 'result',
                    name: 'result',
                    component: Result,
                    props($route) {
                        return {
                            inputTime: $route.params.inputTime,
                            inputValue: $route.params.inputValue,
                            standardAnswer: $route.params.standardAnswer
                        }
                    }
                },
                {
                    path: 'settings',
                    name: 'settings',
                    component: Settings,
                }
            ]
        },
    ]
})
