<template>
    <div class="central">
        <div class="titleBar">自定义设置</div>
        <hr />
        <div class="settingArea">
            <el-form
                ref="form"
                :label-position="labelPosition"
                label-width="100px"
            >
                <el-form-item label="自定义词组">
                    <el-input
                        type="textarea"
                        :autosize="{ minRows: 5, maxRows: 15 }"
                        style="width: 500px"
                        placeholder="请输入内容"
                        v-model="textarea"
                    >
                    </el-input>
                    <div>共{{ newWordList.length }}词</div>
                </el-form-item>
                <el-form-item label="提示符模式">
                    <el-switch
                        v-model="underlineMode"
                        active-color="#8A2BE2"
                        inactive-color="#F2F547"
                        active-text="下划线模式"
                        inactive-text="光标模式"
                    >
                    </el-switch>
                </el-form-item>
                <el-form-item label="单词数量">
                    <el-slider
                        v-model="wordNumbers"
                        :step="5"
                        :min="10"
                        :max="40"
                        :marks="marks"
                        show-stops
                        show-input
                    >
                    </el-slider>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="returnInput"
                        >保存并返回打字模式</el-button
                    >
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
import Vue from "vue"
import { Button, Form, FormItem, Switch, Slider, Message } from "element-ui"
Vue.use(Button)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Switch)
Vue.use(Slider)
export default {
    name: "settings",
    props: ["wordList", "markerMode"],
    data() {
        return {
            textarea: this.wordList.join("|"),
            underlineMode: this.markerMode === "underline",
            labelPosition: "right",
            wordNumbers: 20,
            marks: {
                10: "10",
                15: "15",
                20: "20",
                25: "25",
                30: "30",
                35: "35",
                40: "40",
                45: "45"
            }
        }
    },
    computed: {
        newWordList() {
            return this.textarea.split("|").filter((word) => word.length > 0)
        }
    },
    watch: {
        newWordList: {
            immediate: true,
            deep: true,
            handler(newValue) {
                if (newValue.length < 50) Message("词库不得少于50个词噢")
            }
        }
    },
    methods: {
        returnInput() {
            this.$router.replace({
                name: "input"
            })
            this.$emit("edit", this.newWordList)
            this.$emit("modeSwitch", this.underlineMode)
            this.$emit("changeNumbers", this.wordNumbers)
            console.log(this.wordNumbers)
        }
    }
}
</script>

<style scoped>
.central {
    width: 1200px;
    background-color: rgb(218, 233, 252);
}
hr {
    background-color: rgb(241, 255, 219);
    height: 10px;
    border: none;
}
.titleBar {
    line-height: 60px;
    font-size: 2em;
    padding-left: 30px;
}
.settingArea {
    padding: 30px;
}
</style>