<template>
    <div class="center">
        <div class="box">
            <div :class="item.position" v-for="item in pics" :key="item.id">
                <div class="title">
                    {{ item.title }}
                </div>
                <div class="brief">
                    <div
                        class="para"
                        v-for="(paragraph, i) in item.content"
                        :key="i"
                    >
                        {{ paragraph }}
                    </div>
                </div>
                <div class="mask" @click="jump(item.url)">
                    <div>>>去看一看</div>
                </div>
            </div>
            <div class="leftSwitch" @click="showLeft">
                <i class="el-icon-arrow-left"></i>
            </div>
            <div class="rightSwitch" @click="showRight">
                <i class="el-icon-arrow-right"></i>
            </div>
        </div>
        <div class="bottomNav">
            <div class="bottomSwitch">
                <div
                    :class="item.bottom"
                    @click="showClick(item)"
                    v-for="item in pics"
                    :key="item.id"
                ></div>
            </div>
        </div>
    </div>
</template>

<script>
import Vue from "vue"
import { Icon } from "element-ui"
Vue.use(Icon)
export default {
    data() {
        return {
            currentPicId: 2,
            active: "active" + " picture",
            pics: [
                {
                    title: "数独求解",
                    url: "/sodoku",
                    content: [
                        "这个小项目一开始源于小时候填数独的经历。",
                        "还记得在那个移动互联网没有兴起的时代，大多数网页都还很简陋，对于小孩子而言，吸引力和传统的纸质媒体并没有那么大的差距。在那个时候，周末买一份扬子晚报，兴致勃勃地找到数独填写的那一版，便可以花一个下午反复琢磨格子中的1到9，也不觉得无聊。",
                        "但是，那时运用的方法也就是简单的排除选择，用铅笔在小小的一张纸上一遍遍地填写擦除，总会感觉有一点累。于是一直有一个想法，做一个自动化填写数独的工具。长大以后，接触了编程，也发现了很多数独网站，大多数分为两种：一种是给出题目，仅提供正误判断的功能；还有一种则是简单粗暴，填入题目直接给出答案。前一种网站和手动填其实没什么区别，后一种则太一步到位了，少了中间思考推敲的乐趣，而且给出答案的方法也一般是全局暴力回溯算法，和人类的思路相去甚远。",
                        "于是就萌生了这个想法：做一个辅助数独填写的小工具。这个工具并不会直接给出答案，但是会给出符合规则的备选方案，具体的逻辑推理仍然由玩家自己完成。这就有了这个小项目的第一个工具。"
                    ],
                    id: 1,
                    position: "picture left",
                    bottom: "switchButton inactive"
                },
                {
                    title: "打字练习",
                    url: "/typing",
                    content: [
                        "这个小工具源于大学刚入学的时候练打字的经历。",
                        "大学之前的打字约等于二指禅，因为也没有很大的输入量需求所以也一直没有在意。但是进入大学之后，那些动辄上千上万字的期末论文面前，仍然用二指禅码字无疑是浪费生命。",
                        "因此，进入大学之后不久，我便下定决心学会盲打，并且在知乎上找到了一个推荐的打字练习网站：typing club。这个网站不仅有赏心悦目的UI设计，还提供了循序渐进的指法练习和短文挑战，更对不同的语言有不同的定制化课程。综合而言在当时我找到的一系列打字网站中是使用体验最好的。",
                        "可惜这个网站后来莫名其妙被墙了，虽然仍然可以科学上网继续使用，但总归没那么方便。这个网站的另一个缺点就是，短文中对汉语拼音的处理似乎没有先进行分词操作，基本上是直接逐字进行的，这就导致在遇到多音字情况下会与实际的打字体验有很大的区别。例如，我们都知道”重复“和”重要“的”重“在两个词里面是完全不同的发音，但是因为typing club并没有分词，这两个字的标准答案都是chong。所以就萌生了这个想法：做一个模拟typing club界面的打字练习工具。这个工具同样可以像typing club一样，免选中直接输入拼音（正常的拼音打字一般用空格确认输入法给出的第一个词作为输入内容，免选中的意思相当于你用了一个灵性max的拼音输入法，输入拼音敲下空格就是正确答案）。区别在于，打字的最小单位是按照词语来的，练习内容也并不是一篇一篇的小短文，而是根据词库随机抽取的词组，这样就保证了拼音转换的正确。"
                    ],
                    id: 2,
                    position: "picture middle",
                    bottom: "switchButton active"
                },
                {
                    title: "运筹算法",
                    content: [
                        "这个小工具相对而言要更加硬核一些。",
                        "大学期间我修读过一门课程叫做运筹学，相对而言比较冷门，主要讲授的是运筹优化算法。",
                        "学习这门课程的过程中我发现，油管上关于这些算法的演示教学有很多视频都是印度阿三拍的，而且大多数是手写演示的，没有那么直观。这和我后来刷leetcode时候，官方给出的那些关于算法执行步骤清晰明确的表达大不一样。因此就有了这个想法：做一个自动演示算法执行的小工具。",
                        "但是这个工具和别的工具有不一样的地方。第一，这个工具需要开启服务器才可以运行。我并不是一个科班的学生，所以也懒得去弄一台自己的云服务器（也因为穷），而是把后端的代码上传到了github，clone下来以后命令行运行flask run开启本地服务器即可正常工作，根据你的输入返回相应的结果和代码执行步骤。当然，如果实在懒得做这一步，只是好奇想体验一下，这个工具也提供了一个内置的案例，这个案例的题目和答案都是预先设定好的，因此不需要服务器也能展示结果。第二，这个工具是教学性质的，而不是休闲性质的，对于并没有学过运筹学相关内容的用户来说，可能并不知道这个工具在干什么，所以要求先去了解一下相关的算法步骤。第三，这个工具保留了分数计算，这是目前市面上几乎所有的运筹学求解工具几乎都不具备的一个特点。python中有关于分数处理的包，因此后端语言我使用的是python。采用分数作为数据的保存格式无疑会降低运算效率，工业化的运筹求解包没有一个是这么做的，不过既然是作为教学作用，性能上的考虑自然也就不是问题。如果变量和约束增多，可能后端的运行速度会稍微慢一些，还请您理解。"
                    ],
                    url: "/operation",
                    id: 3,
                    position: "picture right",
                    bottom: "switchButton inactive"
                },
                {
                    title: "敬请期待",
                    content: ["......"],
                    id: 4,
                    position: "picture hidden",
                    bottom: "switchButton inactive"
                }
            ]
        }
    },
    methods: {
        jump(url) {
            this.$router.push(url)
        },
        showLeft() {
            if (this.currentPicId > 1) this.currentPicId--
            this.shiftLeft()
        },
        shiftLeft() {
            this.pics.forEach((pic) => {
                if (pic.position === "picture left")
                    pic.position = "picture hidden"
                else if (pic.position === "picture hidden")
                    pic.position = "picture right"
                else if (pic.position === "picture right")
                    pic.position = "picture middle"
                else if (pic.position === "picture middle")
                    pic.position = "picture left"
            })
        },
        showRight() {
            if (this.currentPicId < 4) this.currentPicId++
            this.shiftRight()
        },
        shiftRight() {
            this.pics.forEach((pic) => {
                if (pic.position === "picture hidden")
                    pic.position = "picture left"
                else if (pic.position === "picture right")
                    pic.position = "picture hidden"
                else if (pic.position === "picture middle")
                    pic.position = "picture right"
                else if (pic.position === "picture left")
                    pic.position = "picture middle"
            })
        },
        showClick(item) {
            console.log(this.currentPicId)
            console.log(item.id)
            if (this.currentPicId === item.id) return
            if (this.currentPicId < item.id) {
                for (let i = 0; i < item.id - this.currentPicId; i++) {
                    this.shiftRight()
                }
            } else if (this.currentPicId > item.id) {
                for (let i = 0; i < this.currentPicId - item.id; i++) {
                    this.shiftLeft()
                }
            }
            this.currentPicId = item.id
            this.pics.forEach((element) => {
                if (element.id == this.currentPicId)
                    element.bottom = "switchButton active"
                else {
                    element.bottom = "switchButton inactive"
                }
            })
        }
    }
}
</script>

<style lang="less" scoped>
.center {
    width: 100%;
    height: 350px;
    position: relative;
}
.box {
    width: 1000px;
    height: 300px;
    position: relative;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.picture {
    transition: 0.3s;
    position: absolute;
    width: 500px;
    height: 250px;
    font-size: 30px;
    background-color: rgb(162, 162, 255);
    border-radius: 20px;
    padding: 20px;
    box-sizing: border-box;

    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    .brief {
        font-size: 16px;
        overflow: hidden;
        padding: 10px;
        box-sizing: border-box;

        .para {
            margin: 5px 0px;
        }
    }

    .mask {
        cursor: pointer;
        width: 100%;
        height: 40%;
        border-radius: 20px;
        position: absolute;
        bottom: 0;
        left: 0;
        background-color: transparent;
        background-image: linear-gradient(
            to bottom,
            transparent,
            rgb(178, 243, 211) 70%,
            rgb(178, 243, 211)
        );
        font-size: 20px;
        padding: 20px;
        color: seagreen;
        box-sizing: border-box;
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
    }
}
.middle {
    z-index: 5;
}
.left,
.right {
    z-index: 4;
    color: rgba(0, 0, 0, 0.5);
    background-color: lavender;
}
.left {
    transform: scale(0.8) translate(-70%, 0);
}
.right {
    transform: scale(0.8) translate(70%, 0);
}
.hidden {
    width: 300px;
    height: 150px;
    z-index: 0;
    background-color: transparent;
    border-radius: 10px;
}
.leftSwitch,
.rightSwitch {
    cursor: pointer;
    color: white;
    font-size: 40px;
    line-height: 60px;
    text-align: center;
    background-color: black;
    opacity: 0.5;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    z-index: 10;
}
.leftSwitch {
    position: absolute;
    left: 50px;
    top: 50%;
    transform: translate(0, -50%);
}
.rightSwitch {
    position: absolute;
    right: 50px;
    top: 50%;
    transform: translate(0, -50%);
}
.bottomNav {
    width: 100%;
}
.bottomSwitch {
    margin: 0 auto;
    display: flex;
    justify-content: space-around;
    width: 500px;
    height: 50px;
}
.switchButton {
    width: 100px;
    height: 20px;
    background-color: cornsilk;
    transition: 0.3s;
}
.switchButton.active {
    background-color: rgb(255, 240, 179);
}
</style>