            :wordNumbers="wordNumbers"
            @edit="editWordList"
            @modeSwitch="editMode"
            @changeNumbers="changeNumbers"
        ></router-view>
    </div>
</template>

<script>
export default {
    name: "typing",
    data() {
        return {
            wordList: [
                "重要",
                "重复",
                "苹果",
                "橘子",
                "香蕉",
                "营地",
                "无限",
                "虚拟",
                "电玩",
                "徽章",
                "北极圈",
                "仁王",
                "怪物",
                "猎人",
                "大佬",
                "开车",
                "代组",
                "亚克力",
                "黄铜",
                "不锈钢",
                "铝锭",
                "注塑",
                "菠萝",
                "霓虹",
                "螺丝",
                "卫星轴",
                "红白机",
                "幻影",
