                "个性",
                "战神"
            ],
            markerMode: "underline",
            wordNumbers: 20,
            disruptedWordList: []
        }
    },
    watch: {
        wordNumbers: {
            immediate: true,
            handler(newValue) {
                console.log(newValue)
                let disruptedList = new Array()
                let rawList = this.wordList.slice()
                for (let i = 0; i < newValue && i < rawList.length; i++) {
                    let index = Math.floor(Math.random() * rawList.length)
                    disruptedList.push(...rawList.splice(index, 1))
                }
                this.disruptedWordList = disruptedList
            }
        }
    },
    methods: {
        editWordList(val) {
            this.wordList = val
        },
        editMode(val) {
            this.markerMode = val ? "underline" : "caret"
        },
        changeNumbers(val) {
            this.wordNumbers = val
        }
    }
}
</script>

